/* src/App.css */
.App {
  font-family: Arial, sans-serif;
  background-color: #e8f5e9;
  color: #388E3C;
  padding: 20px;
}

h1 {
  font-size: 36px;
  text-align: center;
}

h3 {
  font-size: 20px;
  margin-bottom: 10px;
  color: #4CAF50;
}

div {
  background-color: white;
  border-radius: 8px;
  padding: 20px;
  box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.1);
}
